@font-face {
	font-family: 'Inter';
	font-weight: 400;
	font-style: normal;
	font-display: swap;
	src:
		local('Inter Regular'),
		url('../../fonts/Inter-Regular.woff2') format('woff2'),
		url('../../fonts/Inter-Regular.woff') format('woff');
}

